.second-page {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: var(--background-color);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  opacity: 0;

  &.page-animation {
    opacity: 1;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
  }

  > .second-page-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    padding: 6px;
    border-bottom: 3px solid var(--secondary-color);

    > button.back-button {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      height: 100%;
      width: 48px;
      background: none;
      border: none;
      cursor: pointer;
      outline: none;

      &:active {
        background-color: var(--secondary-color);
      }

      span {
        font-size: 24px;
        color: var(--text-color);
      }
    }

    h1 {
      margin: 0;
      font-size: 1.5rem;
      font-weight: 700;
      color: var(--text-color);
    }

    div.second-page-header-space {
      height: 100%;
      width: 48px;
    }
  }

  > .second-page-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    overflow-y: auto;
    padding: 24px;
  }
  transition: 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transition: 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  -moz-transition: 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  -ms-transition: 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  -o-transition: 0.7s cubic-bezier(0.23, 1, 0.32, 1);
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
  -moz-transform: translateX(100%);
  -ms-transform: translateX(100%);
  -o-transform: translateX(100%);
}
