.footer-container {
  z-index: 1000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  padding-bottom: 24px;
  bottom: 0;
  width: 100%;
  background: var(--background-color);
  box-shadow: 0 -12px 12px 12px var(--background-color);

  > span {
    margin-bottom: 2px;
    width: 100%;
    text-align: center;
    font-size: 0.8rem;
    color: var(--text-color-tertiary);
  }
}
